import { useState } from "react";
import "./contact.scss";





export default function Contact() {
  const [message, setMessage] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();
    setMessage(true);
  };

  const contacts = [ "Email: joonyoonlives@gmail.com", "Github: https://github.com/myexistance", "LinkedIn : https://www.linkedin.com/in/myexistance" ]

  const listedContact = contacts.map((contact) =>
       <li>{contact}</li>
       );

  return (
    <div className="contact" id="contact">
      
      <div className="left">
        <h2>Contact.</h2>
        
        <form onSubmit={handleSubmit}>
          <input type="text" placeholder="Email" />
          <textarea placeholder="Message"></textarea>
          <button type="submit">Send</button>
          {message && <span>Thanks, I'll reply ASAP :)</span>}
        </form>
      </div>
      <div className="right">
       

       <ul>{listedContact}</ul>

                     
      </div>
    </div>
  );
}
